<template>
    <div class="same-line">
        <slot></slot>
        <md-button class="md-icon-button md-primary" :disabled="increment === 0" @click="iteract(-1)">
            <md-icon>chevron_left</md-icon>
        </md-button>
        <span>{{ selectedValue.name }}</span>
        <md-button class="md-icon-button md-primary" :disabled="increment === length" @click="iteract(1)">
            <md-icon>chevron_right</md-icon>
        </md-button>
    </div>
</template>

<script>
export default {
  name: 'selectOptions',
  props: {
    arrayOptions: Array
  },
  data () {
    return {
      increment: 0
    }
  },
  computed: {
    length: function () {
      return this.arrayOptions.length - 1
    },
    selectedValue: function () {
      return this.arrayOptions[this.increment]
    }
  },
  methods: {
    iteract: function (number) {
      this.increment += number
    }
  }
}
</script>

<style scoped>

.same-line {
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
}
span {
  width: 115px;
}
</style>



